import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import { Form, Input, Col, Modal, Select, Row } from "antd";

class ModalAddDepartment extends Component {
  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.department;
    if (success) {
      resetFields();
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { department } = this.props;
    const loading = department.loadingSubmit;
    const error = department.errorField ? department.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="departments.add.subtitle.data" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row>
            <Col span={24}>
              {/* <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="departments.parent" />}
              >
                {getFieldDecorator("id_department_parent")(
                  <Select
                    loading={loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                  >
                    {this.props.assetType.asset_categories.map((data, key) => (
                      <Option key={key} value={data.id_department}>
                        {data.department_code +
                          " (" +
                          data.department_name +
                          ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item> */}

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="departments.code" />}
                validateStatus={
                  error.department_code || getFieldError("department_code")
                    ? "error"
                    : null
                }
                help={
                  error.department_code
                    ? error.department_code
                    : getFieldError("department_code")
                }
              >
                {getFieldDecorator("department_code", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 30,
                      message: <IntlMessages id="maxLength30" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="departments.name" />}
              >
                {getFieldDecorator("department_name", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddDepartment = Form.create({
  name: "modal_add_department"
})(ModalAddDepartment);

export default WrappedModalAddDepartment;
