import React, { Component } from "react";

import { Tooltip, Form } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import Button from "../../../../components/uielements/button";
import Popconfirm from "../../../../components/feedback/popconfirm";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import CardWrapper from "../../../App/app.style";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

const getTotal = arr => {
  let totalAmount = 0;
  arr.map(data => {
    totalAmount += parseFloat(data.amount);
  });

  return {
    totalAmount: totalAmount,
  };
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { fixed_asset_expenditures } = props;

    this.state = {
      dataSource: [...fixed_asset_expenditures],
      count: fixed_asset_expenditures.length + 1,
      totalAmount: 0,
      visible: false,
      selectedItem: {}
    };

  }

  componentDidMount() {
    const { 
      temp_fixed_asset_expenditure, 
      temp_fixed_asset_expenditures, 
      clearFixedAssetExpenditure, 
      clearFixedAssetExpenditures,
      sub_ledger_accounts
    } = this.props;

    // milih fixed_asset_expenditure sendiri
    if (temp_fixed_asset_expenditure) {
      const {
        dataSource,
        count,
        totalAmount,
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_account === temp_fixed_asset_expenditure.id_account &&
          currData.id_sub_ledger === temp_fixed_asset_expenditure.id_sub_ledger
      );

      const account = sub_ledger_accounts.find(
        currData => currData.id_account === temp_fixed_asset_expenditure.id_account &&
          currData.id_sub_ledger === temp_fixed_asset_expenditure.id_sub_ledger 
      )

      // fixed_asset_expenditure baru
      if (find < 0) {
        const init = {
          index_no: count,
          amount: 0,
          id_sub_ledger: account.id_sub_ledger,
        };

        const newData = Object.assign({}, temp_fixed_asset_expenditure, init);
        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalAmount: totalAmount,
        });

        clearFixedAssetExpenditure(
          [...dataSource, newData],
          totalAmount
        );
        // console.log(this.props.fixed_asset_expenditure);
      }
    }
    // jika temp dari transaksi sebelumnya
    else if (temp_fixed_asset_expenditures.length !== 0) {
      let newTotAmount = 0;
      let newIndex = 0;

      temp_fixed_asset_expenditures.map(currData => {
        newTotAmount += parseFloat(currData.amount);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...temp_fixed_asset_expenditures],
        totalAmount: newTotAmount,
        count: newIndex
      });
      clearFixedAssetExpenditures(temp_fixed_asset_expenditures, newTotAmount );
    }
  }

  componentDidUpdate(prevProps) {
    const { 
      temp_fixed_asset_expenditure, 
      temp_fixed_asset_expenditures, 
      clearFixedAssetExpenditure, 
      clearFixedAssetExpenditures,
      sub_ledger_accounts,
      id_location,
      clear
    } = this.props;
    //console.log(clear);
    // milih fixed_asset_expenditure sendiri
    if (temp_fixed_asset_expenditure) {
      const {
        dataSource,
        count,
        totalAmount,
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_account === temp_fixed_asset_expenditure.id_account &&
          currData.id_sub_ledger === temp_fixed_asset_expenditure.id_sub_ledger
      );

      const account = sub_ledger_accounts.find(
        currData => currData.id_account === temp_fixed_asset_expenditure.id_account &&
          currData.id_sub_ledger === temp_fixed_asset_expenditure.id_sub_ledger 
      )

      // fixed_asset_expenditure baru
      if (find < 0) {
        const init = {
          index_no: count,
          amount: 0,
          id_sub_ledger: account.id_sub_ledger
        };

        const newData = Object.assign({}, temp_fixed_asset_expenditure, init);
        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalAmount: totalAmount,
        });

        clearFixedAssetExpenditure(
          [...dataSource, newData],
          totalAmount,
        );
        // console.log(this.props.fixed_asset_expenditure);
      }
    }
    // jika fixed_asset_expenditure dari transaksi sebelumnya
    else if (temp_fixed_asset_expenditures.length !== 0) {
      let newTotAmount = 0;
      let newIndex = 0;

      temp_fixed_asset_expenditures.map(currData => {
        newTotAmount += parseFloat(currData.amount);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...temp_fixed_asset_expenditures],
        totalAmount: newTotAmount,
        count: newIndex
      });
      clearFixedAssetExpenditures(temp_fixed_asset_expenditures, newTotAmount);
    }
    else if(clear){
      const {
        dataSource
      } = this.state;
      
      let count=1;
      let totalAmount = 0;
      let newDataSource = [];

      dataSource.map((data) => {
        if(!data.id_sub_ledger){
          totalAmount += data.amount; 
          newDataSource.push({
            ...data,
            index_no: count
          })

          count += 1;
        }
      })

      this.setState({
        dataSource: newDataSource,
        totalAmount,
        count,
      });

      clearFixedAssetExpenditures(newDataSource, totalAmount);
    }
  }

  handleDetailModal = row => {
    const { visible } = this.state;
    // console.log(row);

    visible
      ? this.setState({ visible: false, selectedItem: {} })
      : this.setState({ visible: true, selectedItem: row });
  };

  handleDetail = value => {
    this.setState({ visible: false, selectedItem: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSave } = this.props;
    let newTotAmount = 0;
    let newTotCurrAmount = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotAmount += parseFloat(currData.amount);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalAmount: newTotAmount,
    });
    handleSave(newData, newTotAmount );
  };

  handleSave = row => {
    const { currency_rate } = this.props;
    const newData = this.state.dataSource;
    let newTotAmount = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    //console.log(index);

    //row.amount = parseFloat(row.amount) * rate;

    // console.log(newData);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    // console.log(newData)

    newData.map(currData => {
      newTotAmount += parseFloat(currData.amount);
    });


    this.setState({
      dataSource: [...newData],
      totalAmount: newTotAmount,
    });
    this.props.handleSave(newData, newTotAmount);
  };

  render() {
    let columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: (text, row) => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 3
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({ id: "accountCode"}),
        dataIndex: "account_code",
        width: "30%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "accountName"}),
        dataIndex: "account_name",
        width: "45%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: `${getLanguage({ id: "amount"})} (IDR)`,
        dataIndex: "amount",
        width: "20%",
        align: "center",
        editable: true,
        onCell: record => {
          if (isNaN(record.index_no)) {
            return {
              record,
              dataIndex: "amount",
              title: "Amount",
              width: "15%",
              status: this.props.status
            };
          } else {
            return {
              record,
              editable: true,
              dataIndex: "amount",
              title: "Amount",
              width: "15%",
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
        }
      },
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 1
                // fixed: null
              }
            };
          }
          if (!this.props.status || this.props.status === 1) {
            return (
              <div>
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  onConfirm={() => this.handleDelete(row.index_no)}
                  okText={<IntlMessages id="yes" />}
                  cancelText={<IntlMessages id="no" />}
                >
                  {/* <Button style={{ borderRadius: "50%" }} icon="delete" /> */}
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                  
                </Popconfirm>
              </div>
            );
          }
        }
      }
    ];

    const { dataSource, visible, selectedItem } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const total = getTotal(dataSource);
    const info = {
      index_no: "Total",
      amount: <NumberFormat value={parseFloat(total.totalAmount)} />
    };
    const modifiedData = dataSource.length > 0 ? [...dataSource, info] : [];
    // console.log(modifiedData, total);
    return (
      <CardWrapper>
        <div>
          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
              dataSource={modifiedData}
              columns={columns}
              pagination={false}
              // scroll={{ x: 1300 }}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
