import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import FixedAssetCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/fixedAsset/actions";
import assetCategoryActions from "../../../../redux/asset/assetCategory/actions";
import assetTypeActions from "../../../../redux/asset/assetType/actions";
import departmentActions from "../../../../redux/companyMaster/department/actions";

import { Box } from "../../../App/app.style";
import { Tabs } from "antd";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/fixed-assets";
const { storeAssetCategory } = assetCategoryActions;
const { storeAssetType } = assetTypeActions;
const { storeDepartment } = departmentActions;

class CreateFixedAsset extends Component {
  state = {
    visibleAddAssetCategory: false,
    visibleAddAssetType: false,
    visibleAddDepartment: false
  };

  // Get All data diawal
  componentDidMount() {
    const { createFixedAsset } = this.props;
    createFixedAsset();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.fixedAsset.error) {
      notification("error", this.props.fixedAsset.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.fixedAsset.success) {
      this.props.history.replace(
        redirectTo + "/update/" + this.props.fixedAsset.id_fixed_asset
      );
    }

    // Asset Category
    if (this.props.assetCategory.error) {
      notification("error", this.props.assetCategory.error);
      this.props.clearError();
    }

    if (this.props.assetCategory.success) {
      const { createFixedAsset, clearSuccess } = this.props;
      notification("success", this.props.assetCategory.success);
      this.handleModalAddAssetCategory();
      clearSuccess();
      createFixedAsset();
    }

    // Asset Type
    if (this.props.assetType.error) {
      notification("error", this.props.assetType.error);
      this.props.clearError();
    }

    if (this.props.assetType.success) {
      const { createFixedAsset, clearSuccess } = this.props;
      notification("success", this.props.assetType.success);
      this.handleModalAddAssetType();
      clearSuccess();
      createFixedAsset();
    }

    // Department
    if (this.props.department.error) {
      notification("error", this.props.department.error);
      this.props.clearError();
    }

    if (this.props.department.success) {
      const { createFixedAsset, clearSuccess } = this.props;
      notification("success", this.props.department.success);
      this.handleModalAddDepartment();
      clearSuccess();
      createFixedAsset();
    }
  }

  handleModalAddAssetCategory = () => {
    this.setState({
      visibleAddAssetCategory: !this.state.visibleAddAssetCategory
    });
  };

  handleModalAddAssetType = () => {
    this.setState({
      visibleAddAssetType: !this.state.visibleAddAssetType
    });
  };

  handleModalAddDepartment = () => {
    this.setState({
      visibleAddDepartment: !this.state.visibleAddDepartment
    });
  };

  handleSubmit = value => {
    const { storeFixedAsset } = this.props;
    storeFixedAsset(value);
  };

  handleSubmitAssetCategory = value => {
    const { storeAssetCategory } = this.props;
    storeAssetCategory(value);
  };

  handleSubmitAssetType = value => {
    const { storeAssetType } = this.props;
    storeAssetType(value);
  };

  handleSubmitDepartment = value => {
    const { storeDepartment } = this.props;
    storeDepartment(value);
  };

  render() {
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="fixedAssets.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="fixedAssets.tab.detail" />} key="1">
              <FixedAssetCreateForm
                submit={this.handleSubmit}
                fixedAsset={this.props.fixedAsset}
                permissions={permissions}
                getItems={this.props.getItems}
                user={user}
                // Asset Category
                assetCategory={this.props.assetCategory}
                visibleAddAssetCategory={this.state.visibleAddAssetCategory}
                handleModalAddAssetCategory={this.handleModalAddAssetCategory}
                handleSubmitAssetCategory={this.handleSubmitAssetCategory}
                // Asset Type
                assetType={this.props.assetType}
                visibleAddAssetType={this.state.visibleAddAssetType}
                handleModalAddAssetType={this.handleModalAddAssetType}
                handleSubmitAssetType={this.handleSubmitAssetType}
                // Department
                department={this.props.department}
                visibleAddDepartment={this.state.visibleAddDepartment}
                handleModalAddDepartment={this.handleModalAddDepartment}
                handleSubmitDepartment={this.handleSubmitDepartment}
              />
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    fixedAsset: state.fixedAsset,
    assetCategory: state.assetCategory,
    assetType: state.assetType,
    department: state.department
  };
};

export default compose(
  connect(mapStateToProps, {
    ...actions,
    storeAssetCategory,
    storeAssetType,
    storeDepartment
  })
)(CreateFixedAsset);
