const actions = {
  getFixedAssets: (page, search, filter) => ({
    type: 'GET_FIXED_ASSETS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createFixedAsset: () => ({
    type: 'CREATE_FIXED_ASSET_REQUEST'
  }),
  storeFixedAsset: data => ({
    type: 'STORE_FIXED_ASSET_REQUEST',
    payload: { body: data }
  }),
  storeFixedAssetNote: data => ({
    type: "STORE_FIXED_ASSET_NOTE_REQUEST",
    payload: { body: data }
  }),
  storeFixedAssetMaintenance: data => ({
    type: "STORE_FIXED_ASSET_MAINTENANCE_REQUEST",
    payload: { body: data }
  }),
  editFixedAsset: id_fixed_asset => ({
    type: 'EDIT_FIXED_ASSET_REQUEST',
    payload: { id_fixed_asset: id_fixed_asset }
  }),
  updateFixedAsset: (id_fixed_asset, data) => ({
    type: 'UPDATE_FIXED_ASSET_REQUEST',
    payload: {
      id_fixed_asset: id_fixed_asset,
      body: data
    }
  }),
  updateFixedAssetNote: (id_fixed_asset_note, data) => ({
    type: 'UPDATE_FIXED_ASSET_NOTE_REQUEST',
    payload: {
      id_fixed_asset_note,
      body: data
    }
  }),
  updateFixedAssetMaintenance: (id_fixed_asset_maintenance, data) => ({
    type: 'UPDATE_FIXED_ASSET_MAINTENANCE_REQUEST',
    payload: {
      id_fixed_asset_maintenance,
      body: data
    }
  }),
  showFixedAsset: id_fixed_asset => ({
    type: 'SHOW_FIXED_ASSET_REQUEST',
    payload: { id_fixed_asset: id_fixed_asset }
  }),
  processFixedAsset: id_fixed_asset => ({
    type: "PROCESS_FIXED_ASSET_REQUEST",
    payload: { id_fixed_asset }
  }),
  cancelFixedAsset: (id_fixed_asset, data) => ({
    type: "CANCEL_FIXED_ASSET_REQUEST",
    payload: { 
      id_fixed_asset,
      body : data
    }
  }),
  uploadPicture: (id_fixed_asset, data) => ({
    type: "UPLOAD_FIXED_ASSET_PICTURE_REQUEST",
    payload: {
      id_fixed_asset,
      body : data
    }
  }),
  downloadPicture: id_fixed_asset_picture => ({
    type: "DOWNLOAD_FIXED_ASSET_PICTURE_REQUEST",
    payload: {id_fixed_asset_picture}
  }),
  deletePicture: id_fixed_asset_picture => ({
    type: "DELETE_FIXED_ASSET_PICTURE_REQUEST",
    payload: { id_fixed_asset_picture }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
